@import url('https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans&display=swap');


body{
  width: 100vw;
  display: flex;
  justify-content: center;
  font-size: 16px;
  background: white;
  font-family: 'Open Sans', sans-serif;
  z-index: 1;
}


.App {
  padding:50px;
}

.App-header {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
}
.heading-background{
  position: fixed;
  opacity: 0.05;
  font-size: 20em;
  top:-2vh;
  z-index: -100;
  color:#9352ae;
}
header{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

header h1{
font-size: 2px;
font-weight: 400;  
padding: 20px;
margin-right: 70vw;
}

nav{
  position:fixed;
  top:6vh;
  z-index: 999;
}

nav li{
  display: inline-block;
  cursor: pointer;

}
nav li .link{
  text-decoration: none;
  color: black;
  padding:1em;
  opacity: 0.3;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

li .link:hover{
  opacity: 1;
}

.active{
  color:black;
  opacity: 1;
}

.header-title button{
  background: black;
  border: 0;
  padding: 1em 3em;
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
}

.header-title button:hover{
  opacity: 0.8;
}

.header-title button a{
  font-family: 'Open Sans', sans-serif;
  color:white;
  text-decoration: none;
}

.header-title{
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-size: 3em;
  text-align: left;
  top:2vh;
}

.about{
  display: flex;
  flex-direction: row;
}

.about-content{
  position: relative;
}

.about-content h1{
  font-family: 'Montserrat', sans-serif;
  font-size: 5em;
  text-align: left;
  margin-bottom: 2vh;
}

.about-content p{
  font-size: 1.3em;
  text-align: left;
  width: 37vw;
}

.about img{
  width:50vw;
  height:150vh;
  margin-left: 5vw;
}
.work{
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 50em;
  align-items: flex-start;
}
.heading{
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;  
  font-size: 5em;
}
.work  .work-content{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap: 2em;
}

.project img{
width: 65vw;  
height: 65vh;
}

@media only screen and (max-width : 1300px) {
  
  .project img{
    width: 80vw;  
    height: 58vh;
    }
}

@media only screen and (max-width : 1000px) {

  .about img {
    display: none;
  }
  
  .project img{
    width: 80vw;  
    height: 40vh;
    }
}

@media only screen and (max-width : 800px) {
  
  .project img{
    width: 80vw;  
    height: 35vh;
    }
}

@media only screen and (max-width : 600px) {
  
  .project img{
    width: 80vw;  
    height: 28vh;
    }
}

.project h1{
  font-size: 2em;
}

.project h2{
  font-size: 1em;
}

.contact{
  position: relative;
  top:20vh;
  text-align: left;
  
}
.contact h1{
    font-family: 'Montserrat', sans-serif;
  font-size: 5em;
  text-align: left;
}
.amazing-color{
  color:#9352ae;
  opacity: 1;
}

.contact .email{
  color:black;
  font-size: 2em;
}
.contact li{
  display: inline-block;
  margin-top:0;
  margin-bottom: 0;
  margin-right: 1em;
  margin-left: 0;
}
.contact li a{
  text-decoration: none;
  color:black;
}
.footer{
  position: relative;
  top:2em;
  padding: 1em;
}
.footer a{
  text-decoration: none;
  color:#2c59fb;
}


/* SMARTPHONES PORTRAIT */
@media only screen and (max-device-width : 1025px) {


  body {
    font-size: 6px;
  }

  .heading-background{
    font-size:20em;
  }

  header h1 {

  font-size: 5em;  
  padding: 10px;
  margin-right: 0em;
  }

  nav {
    display: none;
  }

  .header-title{
    font-size: 6em;
    text-align: left;

  }
  .about {
    position: relative;
      display: flex;
  }
  .about img{
    display: none
  }


  .about-content p{
    font-size: 3.5em;
    text-align: left;
    width: 80vw;
  }

  .work{
    align-items: flex-start;
  }

  .project img{
  width: 80vw;  
  height: 30vh;
  }

  .project h1{
    font-size: 4em;
  }
  .project h2{
    font-size: 3em;
  }

  .contact{
    top:15vh;
    left:2vw;
  }
  .contact h1{
    font-size: 4em;
  }
  .contact .email{
    font-size: 3.5em;
  }

  .contact li{
    display: block;
    padding: 2em;
  }
  .contact li a{
    font-size: 2.5em;
  }
  .footer{
    top:2vh;
    padding: 1em;
    font-size: 2em;
  }


}




/* Large screens ----------- */
@media only screen  and (min-width : 1824px) {
/* Styles */



body{
    font-size: 20px;
  }
  header h1{
    font-size: 3em;
    margin-right: 25em;
  }


  nav{
  left:60em;
  top:12em;
  z-index: 100;
}
nav li{
   font-size: 3em;

}
.header-title{
  font-size: 5em;
  top:1em;
  left:1em;
}
.about{position: relative;
      display: flex;
      top:-20em;
}
.about-content{
  left:10em;
}
.about-content p{
  font-size: 3.5em;
  text-align: left;
  width: 25em;
}
.about img{
  margin-left: 1em;
}

.work{
  left:10em;
  top:4em;
  align-items: flex-start;
}
.work .work-content{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.project img{
width: 32em;  
height: 32em;
}

.contact{
  top:10em;
  left:8em;
}
.contact h1{
  font-size: 8em;
}
.contact .email{
  font-size: 5em;
}

.contact li a{
  font-size: 4em;
  padding: 1em;
}
.footer{
  top:4em;
  padding: 1em;
  font-size: 4em;
}


}


